import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryAhamoveComponent } from './delivery-ahamove.component';
import { CreateBillRetailAhamoveComponent } from './create-bill-retail-ahamove/create-bill-retail-ahamove.component';
import { CreateBillCustomerAhamoveComponent } from './create-bill-customer-ahamove/create-bill-customer-ahamove.component';
import { DetailBillAhamoveComponent } from './detail-bill-ahamove/detail-bill-ahamove.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ChooseMethodAhamoveComponent } from './components/choose-method-ahamove/choose-method-ahamove.component';
import { ListAddressCustomerAhamoveComponent } from './components/list-address-customer-ahamove/list-address-customer-ahamove.component';
import { AddAddressCustomerAhamoveComponent } from './components/add-address-customer-ahamove/add-address-customer-ahamove.component';
import { EditAddressCustomerAhamoveComponent } from './components/edit-address-customer-ahamove/edit-address-customer-ahamove.component';

const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: DeliveryAhamoveComponent
      },
      {
        path: "order-bill-retail-ahamove",
        component: CreateBillRetailAhamoveComponent
      },
      {
        path: "order-bill-customer-ahamove",
        component: CreateBillCustomerAhamoveComponent
      },
      {
        path: "detail-bill",
        component: DetailBillAhamoveComponent
      }
    ]
  }

]

@NgModule({
  declarations: [DeliveryAhamoveComponent, CreateBillRetailAhamoveComponent, CreateBillCustomerAhamoveComponent, DetailBillAhamoveComponent, ChooseMethodAhamoveComponent, ListAddressCustomerAhamoveComponent, EditAddressCustomerAhamoveComponent, AddAddressCustomerAhamoveComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule
  ]
})
export class DeliveryAhamoveModule { }
