import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-edit-address-customer-ahamove',
  templateUrl: './edit-address-customer-ahamove.component.html',
  styleUrls: ['./edit-address-customer-ahamove.component.scss']
})
export class EditAddressCustomerAhamoveComponent implements OnInit {
  @Input("visible_edit_address_ahamove") visible_edit_address_ahamove: boolean = false;
  @Output("closeAndGetValueEditAddress") closeAndGetValueEditAddress = new EventEmitter<any>();
  @Input("data_edit_address_ahamove") data_edit_address_ahamove;
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales
  ) {
  }
  addInformation: FormGroup;
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      address: new FormControl(this.data_edit_address_ahamove.infoAddressChose.address, Validators.required),
      province: new FormControl(this.data_edit_address_ahamove.infoAddressChose.province, Validators.required),
      district: new FormControl(this.data_edit_address_ahamove.infoAddressChose.district, Validators.required),
      ward: new FormControl(this.data_edit_address_ahamove.infoAddressChose.ward, Validators.required),
    })
  }
  exportAddress() {
    let data = {
      _id: this.data_edit_address_ahamove.infoAddressChose._id,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      type: 3,
    }
    return data;
  }
  goBack() {
    this.closeAndGetValueEditAddress.emit(false);
  }
  updateAddress() {
    this.vhQuerySales.updateAddress(this.data_edit_address_ahamove.infoAddressChose._id, this.exportAddress()).then((res) => {
      if (res) {
        this.closeAndGetValueEditAddress.emit( this.exportAddress());
      } else {
        this.vhComponent.showToast(2000, this.languageService.translate("An error occurred while udpating address"), "alert-toast")
      }
    })
  }
  deleteAddress() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to delete address ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.deleteAddress(this.data_edit_address_ahamove.infoAddressChose._id).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.vhComponent.showToast(2000, this.languageService.translate("This address has been deleted successfully"), "success-toast")
                this.closeAndGetValueEditAddress.emit(true);
              }
            })
          })
        })
      })
      .catch(() => { })
  }

}
