import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { DeliveryA45Component } from 'src/app/sales/components/print/delivery/A45/delivery-a45/delivery-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-create-bill-retail-ahamove',
  templateUrl: './create-bill-retail-ahamove.component.html',
  styleUrls: ['./create-bill-retail-ahamove.component.scss']
})
export class CreateBillRetailAhamoveComponent implements OnInit {

  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private ahamoveService: AhamoveService,
  ) {
    this.billInfo = this.router.getCurrentNavigation().extras.state.billInfo;
    this.data_delivery = this.router.getCurrentNavigation().extras.state.data_delivery;
    this.dataSearchAndDate = this.router.getCurrentNavigation().extras.state.dataSearchAndDate
  }
  dataSearchAndDate: any;
  billInfo: any;
  billDetails: Array<any> = [];
  infoCustomer: any;
  addInformation: FormGroup;
  deliveryInfo: FormGroup;
  data_delivery: any
  service: any;
  chooseService: any = {};
  cod_amount: any;
  invoice: VhSaleInvoices;
  open_status: boolean = false;
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.minLength(6)]),
      phone: new FormControl('', [Validators.required]),
      address: new FormControl('', Validators.required),
      province: new FormControl('', Validators.required),
      district: new FormControl('', Validators.required),
      ward: new FormControl('', Validators.required),
    })
    this.deliveryInfo = new FormGroup({
      cod: new FormControl('0', [Validators.required, Validators.pattern("^[0-9,]{1,10}$")]),
      transport: new FormControl('', Validators.required),
      note: new FormControl(''),
    })
    this.getData();
  }
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.cod_amount = this.vhAlgorithm.vhnumeral('.cod_amount');
    });
  }
  //ly d liu billDetail
  getData() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill_details_byId_bill(this.billInfo._id),
        this.vhQuerySales.getlocalAddresss(),
      ]).then((res: any) => {
        if (res[0]) {
          this.billDetails = res[0].map(item => {
            let name_product = this.vhQuerySales.getlocalDetailProduct(item.id_subproduct ? item.id_subproduct : item.id_product).name;
            return {
              ...item,
              name_product: name_product,
            }
          })
          this.invoice = new VhSaleInvoices(this.billInfo, this.billDetails, this.vhQuerySales);
        }
        if (res[1]) {
          let list_address_pickups = res[1].filter(item => item.id_delivery == this.data_delivery._id && item.type == 4);
          if (list_address_pickups.length) {
            let data_address_pickup = list_address_pickups.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
            this.data_delivery = {
              ...this.data_delivery,
              pick_name: data_address_pickup.name,
              pick_tel: data_address_pickup.phone,
              pick_address: data_address_pickup.address,
              pick_province: data_address_pickup.province,
              pick_district: data_address_pickup.district,
              pick_ward: data_address_pickup.ward,
            }
          }
        }
        let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
        if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
        this.open_status = true;
      }).catch(err => {
        this.vhComponent.hideLoading(0);
        this.vhComponent.showToast(2000, this.languageService.translate('An error occurred while getting bills'), "alert-toast")
      })
    }).finally(() => { this.vhComponent.hideLoading(0) })
  }
  checkPrinter: boolean = false;
  goBack(success?) {
    if (success) {
      this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ahamove"], {
        state: {
          dataSearchAndDate: this.dataSearchAndDate
        }
      })
    } else {
      this.vhComponent
        .alertConfirm(
          (this.languageService.translate("Confirm")),
          (this.languageService.translate("Changes will not be saved")),
          "",
          "OK",
          (this.languageService.translate("Cancel"))
        )
        .then(
          () => {
            this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ahamove"], {
              state: {
                dataSearchAndDate: this.dataSearchAndDate
              }
            })
          },
          () => { }
        );
    }
  }
  prepareProducts() {
    let data = this.billDetails.map(item => {
      return {
        name: item.name_product,
        quantity: item.quantity
      }
    })
    return data;
  }
  prepareOrder() {
    let data = {
      token: this.data_delivery.token,
      order_time: 0,
      service_id: this.chooseService._id,
      pick_address: this.formatAddress(this.data_delivery.pick_address, this.data_delivery.pick_ward, this.data_delivery.pick_district, this.data_delivery.pick_province),
      address: this.formatAddress(this.addInformation.value.address, this.addInformation.value.ward, this.addInformation.value.district, this.addInformation.value.province),
      pick_name: this.data_delivery.pick_name,
      pick_tel: this.data_delivery.pick_tel,
      phone: this.addInformation.value.phone,
      name: this.addInformation.value.name,
      note: this.deliveryInfo.value.note ? this.deliveryInfo.value.note : "",
      cod: this.cod_amount.getRawValue()
    }
    return data;
  }
  createOrder(print?) {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to create order ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.ahamoveService.createOrderAhamove(this.prepareOrder()).then((res: any) => {
            this.invoice.setOrderCode(res.order_id);
            this.vhQuerySales.updateBill_Billdetail(this.invoice.getID(), this.invoice.getCreateUpdateInvoice(1), this.invoice.getCreateUpdateInvoiceDetail()).then((data: any) => {
              this.vhComponent.hideLoading(0).then(() => {
                if (print) {
                  this.showPrint(res);
                  this.vhComponent.showToast(2000, this.languageService.translate('Create order successfully !'), "success-toast")
                }
                else {
                  this.vhComponent.showToast(2000, this.languageService.translate('Create order successfully !'), "success-toast")
                  this.goBack(true)
                }
              })
            })
          }).catch(err => {
            this.vhComponent.hideLoading(0);
            if (err.error.message == "Request failed with status code 406") {
              this.vhComponent.showToast(2000, this.languageService.translate(" s dng dch v tin thu h vui lng lin h vi Viethas !"), "alert-toast")
            }
            else {
              this.vhComponent.showToast(2000, this.languageService.translate("An error occurred while creating order, please check the information !"), "alert-toast")
            }
          })
        })
      }).catch(() => { })
  }
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  showPrint(res) {
    this.customDataBillInfo(res)
    this.checkPrint().then((printer) => {
      this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
        billInfo: this.billInfo,
        billDetails: this.billDetails,
        data_delivery: this.data_delivery,
        printer
      }, false, false, "modal-print").then(modal => {
        modal.onWillDismiss().then(() => {
          this.goBack(true);
        })
      })

    }, (err) => {
      this.goBack(true)
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type) {
    switch (type) {
      default: return DeliveryA45Component
    }
  }
  customDataBillInfo(res) {
    this.billInfo = {
      ...this.billInfo,
      order_code: res.order_id,
      data: {
        created: res.order.order_time * 1000,
        ship_money: res.order.total_fee,
        insurance: 0,
        pick_money: res.order.path[1].cod ? res.order.path[1].cod : 0,
        customer_fullname: res.order.path[1].name,
        customer_tel: res.order.path[1].mobile,
        address: res.order.path[1].address,
        is_freeship: 0
      }
    }
  }
  formatCityId(name) {
    name = name.toLowerCase().trim();
    let city_id = "0";
    switch (name) {
      case 'hochiminh': case 'hcm': case 'thnh ph h ch minh': case 'tphcm': case 'h ch minh':
      case 'thanh pho hcm': case 'thanh pho ho chi minh': case 'ho chi minh': case 'tp ho chi minh':
      case 'tp hcm': case 'thanh pho h ch minh': case 'tp.hcm': case 'h chi minh': case 'h ch minh': city_id = "SGN"; break;
      case 'hanoi': case 'hn': case 'thnh ph h ni': 'th  h ni'; break;
      case 'thanh pho hn': case 'thanh pho ha noi': case 'ha noi': case 'tp ha noi':
      case 'tp hn': case 'thanh pho h ni': case 'tp.hn': case 'tphn': case 'h ni':
      case 'h noi': case 'ha ni': case 'h ni': city_id = "HAN"; break;
      default: city_id = "0";
    }
    return city_id;
  }
  formatAddress(address, ward, district, province) {
    return address + ", " + ward + ", " + district + ", " + province;
  }
  // ------modal choose--------
  chooseMethodAhamove: boolean = false;
  dataToGetMethodAhamove: any;
  openChooseMethodAhamove() {
    let pick = this.formatCityId(this.data_delivery.pick_province);
    let receive = this.formatCityId(this.addInformation.value.province);
    if (pick != "0" && receive != "0") {
      this.dataToGetMethodAhamove = {
        city_id: pick,
        pick_address: this.formatAddress(this.data_delivery.pick_address, this.data_delivery.pick_ward, this.data_delivery.pick_district, this.data_delivery.pick_province),
        receive_address: this.formatAddress(this.addInformation.value.address, this.addInformation.value.ward, this.addInformation.value.district, this.addInformation.value.province),
        token: this.data_delivery.token,
        cod: +this.cod_amount.getRawValue(),
      }
    }
    this.chooseMethodAhamove = true;
  }
  cancelModalMethodAhamove(event) {
    if (event) {
      this.chooseService = event;
      this.deliveryInfo.controls.transport.setValue(this.chooseService._id);
    }
    this.chooseMethodAhamove = false;
  }
}
