import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { DeliveryA45Component } from 'src/app/sales/components/print/delivery/A45/delivery-a45/delivery-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail-bill-ahamove',
  templateUrl: './detail-bill-ahamove.component.html',
  styleUrls: ['./detail-bill-ahamove.component.scss']
})
export class DetailBillAhamoveComponent implements OnInit {

  constructor(
    private languageService: LanguageService,
    private router: Router,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private ahamoveService: AhamoveService,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
  ) {
    this.billInfo = this.router.getCurrentNavigation().extras.state.billInfo;
    this.data_delivery = this.router.getCurrentNavigation().extras.state.data_delivery;
    this.dataSearchAndDate = this.router.getCurrentNavigation().extras.state.dataSearchAndDate
  }
  billInfo: any;
  inforCustomer: any;
  data_delivery: any;
  billDetail: any;
  dataSearchAndDate: any;
  ngOnInit() {
    this.getData();
  }
  getData() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getBill_details_byId_bill(this.billInfo._id).then((res: any) => {
        this.vhComponent.hideLoading(0).then(() => {
          this.billDetail = res.map((item: any) => {
            let name_product = this.vhQuerySales.getlocalProduct(item.id_product).name;
            return {
              ...item,
              name_product: name_product,
            }
          })
          this.billInfo.name_service = this.filterTitleServiceAhamove(this.billInfo.data.service._id);
        })
      })
    })
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-ahamove"], {
      state: { dataSearchAndDate: this.dataSearchAndDate }
    })
  }
  cancelOrder() {
    if (this.vhAuth.checkMyPermission("shipping_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate('Confirm'), "", this.languageService.translate("Do you want to delete shipping bill ?"), "Ok", this.languageService.translate("Cancel")).then(() => {
        this.vhComponent.showLoading('', 'transparent-loading');
        this.ahamoveService.cancelOrderAhamove(this.data_delivery.token, this.billInfo.order_code).then(() => {
          this.vhComponent.hideLoading(0).then().then(() => {
            this.vhComponent.showToast(2000, this.languageService.translate("Delete bill successfully"), "success-toast")
            this.goBack();
          })
        }).catch(err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(2000, this.languageService.translate("An error occurred while deleting bill"), "alert-toast")
        })
      }).catch(() => { })
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }
  filterTitleServiceAhamove(id) {
    let title = '';
    let description = '';
    switch (id) {
      case 'SGN-BIKE': title = 'Over speed'; description = 'Deliver within an hour'; break;
      case 'SGN-EXPRESS': title = 'Near delivery'; description = 'Super fast delivery only 30 minutes'; break;
      case 'SGN-POOL': title = 'Dirt cheap'; description = 'Save 20%, 2-hour delivery'; break;
      case 'SGN-SAMEDAY': title = '4-Hour'; description = 'Delivery within the city, committed in 4 hours'; break;
      case 'SGN-TRICYCLE': title = 'Delivery tricycle'; description = 'Tricycle service description'; break;
      case 'SGN-TRUCK-500': title = '500kg truck'; description = 'Delivered by small truck'; break;
      case 'SGN-TRUCK-2000': title = '2000kg truct'; description = 'Delivered by big truck'; break;
      case 'HAN-BIKE': title = 'Over speed'; description = 'Deliver within an hour'; break;
      case 'HAN-EXPRESS': title = 'Near delivery'; description = 'Super fast delivery only 30 minutes'; break;
      case 'HAN-POOL': title = 'Dirt cheap'; description = 'Save 20%, 2-hour delivery'; break;
      case 'HAN-SAMEDAY': title = '4-Hour'; description = 'Delivery within the city, committed in 4 hours'; break;
    }
    return title;
  }
  /*----------Print------------------*/
   /**
   * ly thng tin d liu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

   /** kim tra d liu my in v show component in tng ng
   * @example this.showPrint() 
   */
  showPrint() {
    this.checkPrint().then((printer) => {
      this.customDataBillDetail(this.billDetail);
      this.customDataBillInfo();
      this.vhComponent.showModal(this.renderPrintPage(printer['type']), {
        billInfo: this.billInfo,
        billDetails: this.billDetail,
        printer,
        data_delivery: this.data_delivery
      }, false, false, "modal-print")
    }, (err) => {
      let name :any= 'Sales'
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/sales/dashboard/manage/printer','page_desktop_sales'],{state : name}))
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'K57' | 'K80' |'K80-3' | 'A4') {
    switch (type) {
      default: return DeliveryA45Component
    }
  }
  customDataBillDetail(billDetails) {
    let data: any = billDetails.map(item => {
      return {
        name_product: item.name,
        quantity: item.quantity
      }
    })
    return data;
  }
  customDataBillInfo() {
    this.billInfo.data.created = this.billInfo.data.order_time * 1000;
    this.billInfo.data.ship_money = this.billInfo.data.total_fee;
    this.billInfo.data.insurance = 0;
    this.billInfo.data.pick_money = this.billInfo.data.path[1].cod ? this.billInfo.data.path[1].cod : 0;
    this.billInfo.data.customer_fullname = this.billInfo.data.path[1].name;
    this.billInfo.data.customer_tel = this.billInfo.data.path[1].mobile;
    this.billInfo.data.address = this.billInfo.data.path[1].address;
    this.billInfo.data.is_freeship = 0;
  }

}
