import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add-address-customer-ahamove',
  templateUrl: './add-address-customer-ahamove.component.html',
  styleUrls: ['./add-address-customer-ahamove.component.scss']
})
export class AddAddressCustomerAhamoveComponent implements OnInit {
  @Input("visible_add_address_ahamove") visible_add_address_ahamove: boolean = false;
  @Output("closeAndGetValueAddAddress") closeAndGetValueAddAddress = new EventEmitter<any>();
  @Input("data_add_address_ahamove") data_add_address_ahamove;

  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales
  ) {
  }
  addInformation: FormGroup;
  province: any = {
    ProvinceName: ''
  };
  district: any = {
    DistrictName: '',
  };
  ward: any = {
    WardName: ''
  };
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      address: new FormControl('', Validators.required),
      province: new FormControl('', Validators.required),
      district: new FormControl('', Validators.required),
      ward: new FormControl('', Validators.required),
    })
  }
  createAddress() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to create address Ahamove ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        let checkAddress: any = this.checkAddress(this.addInformation.value);
        if (checkAddress.result) {
          this.vhComponent.showToast(2000, this.languageService.translate("This address already exists"), "alert-toast")
        } else {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQuerySales.addAddress(this.exportAddress()).then(res => {
              this.vhComponent.hideLoading(0).then(() => {
                if (res) {
                  this.closeAndGetValueAddAddress.emit(res);
                }
              })
            })
          })
        }
      })
      .catch(() => { })
  }
  exportAddress() {
    let data = {
      id_customer: this.data_add_address_ahamove.billInfo.id_customer,
      type: 3,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      date: new Date().toISOString(),
    }
    return data;
  }
  /** 
  * hm ny dng  check xem a ch ca khch hng  c trong database hay cha
  * result : true l a ch  c lu khng cn lu li, false tc l phi lu a ch  li
  * */
  checkAddress(data) {
    let result: boolean = false;
    let id_address;
    //duyt for trng hp trng a ch th return true  khi lu li
    for (let i in this.data_add_address_ahamove.infoAddress) {
      if (this.data_add_address_ahamove.infoAddress[i].address == data.address && this.data_add_address_ahamove.infoAddress[i].ward == data.ward) {
        result = true;
        id_address = this.data_add_address_ahamove.infoAddress[i]._id
      }
    }
    return {
      result: result,
      id_address: id_address
    };
  }
  goBack() {
    this.closeAndGetValueAddAddress.emit(false);
  }
}
