import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-list-address-customer-ahamove',
  templateUrl: './list-address-customer-ahamove.component.html',
  styleUrls: ['./list-address-customer-ahamove.component.scss']
})
export class ListAddressCustomerAhamoveComponent implements OnInit {

  @Input("visible_list_address") visible_list_address: boolean = false;
  @Output("onCloseListAddress") onCloseListAddress = new EventEmitter<any>();
  @Input("data_list_address_ahamove") data_list_address_ahamove;
  constructor(
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent
  ) { }
  infoAddress: Array<any> = [];
  ngOnInit() {

    this.getData();
  }
  getData() {
    this.vhQuerySales.refreshLocalAddresss().then(() => {
      this.infoAddress = this.vhQuerySales.getlocalAddresss().filter(item => item.type == 3 && item.id_customer == this.data_list_address_ahamove.billInfo.id_customer).reverse();
    })
  }
  goBack() {
    this.onCloseListAddress.emit(false);
  }
  chooseAddress(item) {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to use this address ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.updateAddress(item._id, { date: new Date().toISOString() }).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.onCloseListAddress.emit(item);
              }
            })
          })
        })
      })
      .catch(() => { })
  }
  // -------drawer add adress-------
  visible_add_address_ahamove: boolean = false;
  data_add_address_ahamove: any;
  addAddress() {
    this.data_add_address_ahamove = {
      billInfo: this.data_list_address_ahamove.billInfo,
      data_delivery: this.data_list_address_ahamove.data_delivery,
      infoAddress: this.infoAddress
    }
    this.visible_add_address_ahamove = true;
  }
  closeAndGetValueAddAddress(event) {
    if (event) {
      this.getData();
      this.onCloseListAddress.emit(event);
    }
    this.visible_add_address_ahamove = false;
  }
  // -------drawer add adress-------
  visible_edit_address_ahamove: boolean = false;
  data_edit_address_ahamove: any;
  editAddress(item) {
    this.data_edit_address_ahamove = {
      billInfo: this.data_list_address_ahamove.billInfo,
      data_delivery: this.data_list_address_ahamove.data_delivery,
      infoAddress: this.infoAddress,
      infoAddressChose: item
    }
    this.visible_edit_address_ahamove = true;
  }
  closeAndGetValueEditAddress(event) {
    if (event) {
      this.getData();
      this.onCloseListAddress.emit(event);
    }
    this.visible_edit_address_ahamove = false;
  }
}
